<template>
	<div class="operation-btn">
		<div class="btn-item" v-for="(item,index) in btnArr" :key="index" :class="{operationactive:currentIndex==index}" @click="itemClick(index)">{{item}}</div>
	</div>
</template>

<script>
export default {
  name: 'OperationNavsBtn',
  components:{
  },
  data(){
	return {
		btnArr:[
			'今日',
			'昨日',
			'近7日',
			'近30日',
		],
		currentIndex:0
	}
  },
  methods:{
	itemClick(index){
		if(this.currentIndex===index)return
		this.currentIndex=index
		this.$emit("itemClick",this.currentIndex)
	}
  }
}
</script>


