		</div>
		<div class="operation-sell-box">
			<!--今日，昨日，近7日，近30日-->
			<operation-navs-btn @itemClick="itemClick"/>
			<operation-navs-info :type-data="typeData"/>
		</div>
		<!--销售-->
		<div class="operation-sell-box"></div>
		<!--采购-->
		<div class="operation-sell-box"></div>
	</div>
</template>

<script>
import OperationNavsBtn from './OperationNavsBtn'
import OperationNavsInfo from './OperationNavsInfo'

export default {
  name: 'OperationContent',
  components:{
	OperationNavsBtn,
	OperationNavsInfo
  },
  data(){
	return{
		test:[
			{value:"今日"},
			{value:"昨日"},
			{value:"近7日"},
			{value:"近30日"},
		],
		currentType:'today'
	}
  },
  props:{
	operationInfo:{
		type:Object,
		default(){
			return{}
		}
	},
  },
  methods:{
	itemClick(index){
		//按钮点击
		//经营状况是一个所有订单的总和
		switch(index){
			case 0:
				this.currentType="today";
			break;
			case 1:
				this.currentType="yestoday";
			break;
			case 2:
				this.currentType="serveday";
			break;
			case 3:
				this.currentType="sanshiday";
			break;
		}
	}
  },
  computed:{
	value(){
		return this.test[this.currentType]
	},
	typeData(){
		return this.operationInfo[this.currentType]
	}
  },
}
</script>


